
import { defineComponent,reactive,toRefs,provide } from 'vue'
import IndexNav from '@/components/index/IndexNav.vue'
export default defineComponent({
  name:'Index',
  components:{
    IndexNav
  },
  setup () {
    let state = reactive({
      newalbumOn:false,
      timedate:''
    })
    provide('newalbumLeftData',state)
    return {
      ...toRefs(state),
    }
  }
})
